import SearchFilters from "./filters";
import SearchResults from "./results";

export default function SearchMain() {
  return (
    <div className="bg-light">
      <div className="container">
        <div className="row">
          <div className="col-12 col-md-3 py-4">
            <SearchFilters />
          </div>
          <div className="col-12 col-md-9 bg-white p-4 px-lg-5 border-start border-end">
            <SearchResults />
          </div>
        </div>
      </div>
    </div>
  );
}

export function makeSearchUrl({ query, page, sort, type, filters }) {
  const qs = new URLSearchParams();
  const qItems = [];
  for (const [key, value] of Object.entries(filters || {})) {
    qItems.push(`${key}:${value.includes(" ") ? `"${value}"` : value}`);
  }
  if (query) qItems.push(query);
  if (qItems.length) qs.set("q", qItems.join(" "));
  if (page) qs.set("p", page);
  if (sort) qs.set("s", sort);
  if (type) qs.set("t", type);
  return "/search?" + qs.toString();
}
