import Head from "../../components/head";
import Humanize from "../../components/humanize";
import Pagination from "../../components/pagination";
import SearchExamplesModal from "./examples-modal";
import SearchResultList from "./result-list";
import SearchSortButton from "./sort-button";
import Spinner from "../../components/spinner";
import { useEffect } from "react";
import { useSearchParams } from "react-router-dom";
import { useSearchResult } from "./../../data";

export default function SearchResults() {
  const [searchParams] = useSearchParams();
  const searchQuery = searchParams.get("q");
  const { result, isLoading, isError } = useSearchResult({
    query: searchQuery,
    page: searchParams.get("p"),
    sort: searchParams.get("s"),
    filters: {
      type: searchParams.getAll("t"),
      tier: searchParams.getAll("tr"),
      framework: searchParams.getAll("f"),
    },
  });
  useEffect(() => {
    window.scrollTo(0, 0);
  }, [searchParams]);

  if (isLoading || isError) return <Spinner />;
  const makePaginationUrl = (page) => {
    const qs = new URLSearchParams(searchParams);
    qs.set("p", page);
    return "/search?" + qs.toString();
  };
  return (
    <main className="main-search">
      <Head title={searchQuery ? `Search: ${searchQuery}` : "Search"} />
      <div className="d-flex flex-wrap gap-2 pt-1 small text-muted">
        <div className="flex-fill">
          Found <Humanize type="counter" value={result.total} />{" "}
          {result.total === 1 ? "package" : "packages"}
        </div>
        <div>
          <SearchSortButton />
        </div>
      </div>
      <SearchExamplesModal />
      {result.total ? (
        <>
          <hr />
          <SearchResultList items={result.items} />
          <div className="pt-3">
            <Pagination
              total={result.total}
              currentPage={result.page}
              resultsPerPage={result.limit}
              makePageUrl={makePaginationUrl}
            />
          </div>
        </>
      ) : (
        <div className="text-center pt-4">
          <h5 className="card-title">Nothing has been found by your request</h5>
          <p className="card-text">
            Try a less-specific search or use truncation (or wildcard) operator{" "}
            <code>*</code>
          </p>
          <button
            type="button"
            className="btn btn-primary"
            style={{ verticalAlign: "inherit" }}
            data-bs-toggle="modal"
            data-bs-target="#searchExamplesModal"
          >
            Search Examples
          </button>
        </div>
      )}
    </main>
  );
}
