import { useNavigate, useSearchParams } from "react-router-dom";
import Icon from "../../components/icon";
import PropTypes from "prop-types";
import { useState } from "react";

export default function SearchFilterGroup(props) {
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();
  const qItems = searchParams.getAll(props.id) || [];
  const [expanded, setExpanded] = useState(true);

  const onChange = (filterId, checked) => {
    const qs = new URLSearchParams(searchParams);
    qs.delete(props.id);
    const newItems = checked
      ? [...qItems, ...[filterId]]
      : qItems.filter((value) => value !== filterId);
    newItems.forEach((value) => qs.append(props.id, value));
    navigate("/search?" + qs.toString());
  };

  return (
    <>
      <li className="nav-item pt-3">
        <div className="d-flex align-items-center">
          <div className="pe-3 text-muted small">{props.name}</div>
          <div className="flex-fill">
            <hr />
          </div>
          <div>
            <button
              className="btn btn-sm btn-toggle ps-2 text-muted"
              data-bs-toggle="collapse"
              data-bs-target={`#filter-collapse-${props.id}`}
              aria-expanded="true"
              aria-controls={`filter-collapse-${props.id}`}
              onClick={() => setExpanded(!expanded)}
            >
              <Icon name={`caret-${expanded ? "down" : "up"}-fill`} size="12" />
            </button>
          </div>
        </div>
      </li>
      <li className="nav-item collapse show" id={`filter-collapse-${props.id}`}>
        {props.items.map((item) => (
          <div key={item.id} className="form-check">
            <input
              className="form-check-input"
              type="checkbox"
              id={`filter-input-${props.id}-${item.id}`}
              checked={qItems.includes(item.id)}
              onChange={(e) => onChange(item.id, e.target.checked)}
            />
            <label
              className="form-check-label"
              htmlFor={`filter-input-${props.id}-${item.id}`}
            >
              {item.name}
            </label>
          </div>
        ))}
      </li>
    </>
  );
}

SearchFilterGroup.propTypes = {
  id: PropTypes.string.isRequired,
  name: PropTypes.string.isRequired,
  items: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.string.isRequired,
      name: PropTypes.string.isRequired,
      description: PropTypes.string,
    })
  ),
};
