import { useEffect, useRef, useState } from "react";
import { useNavigate, useSearchParams } from "react-router-dom";

export default function SearchForm(props) {
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();
  const [inputValue, setInputValue] = useState(searchParams.get("q") || "");
  const [searchFocused, setSearchFocused] = useState(false);
  const inputRef = useRef(null);

  const onDidSearch = (event) => {
    const qs = new URLSearchParams(searchParams);
    ["p", "q"].forEach((key) => qs.delete(key));
    if (inputValue) qs.set("q", inputValue);
    navigate(`/search?${qs.toString()}`);
    inputRef.current.blur();
    event.preventDefault();
  };

  const onDidKeyDown = (event) => {
    if (event.keyCode === 191 && inputRef.current) {
      inputRef.current.focus();
      setSearchFocused(true);
      event.preventDefault();
      document.removeEventListener("keydown", _kdListener);
    }
    if (event.keyCode === 27) {
      inputRef.current.blur();
    }
  };
  const _kdListener = onDidKeyDown.bind(this);

  useEffect(() => {
    document.addEventListener("keydown", _kdListener);
    return function cleanup() {
      document.removeEventListener("keydown", _kdListener);
    };
  });

  return (
    <form
      role="search"
      className="pt-2 pt-lg-0 pe-lg-2"
      onSubmit={(event) => onDidSearch(event)}
    >
      <div
        className={`float-end position-relative ${
          searchFocused ? "invisible" : "visible"
        }`}
      >
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width="22"
          height="20"
          aria-hidden="true"
          className="position-absolute"
          style={{ right: "0.2rem", top: "0.37rem" }}
        >
          <path
            fill="none"
            stroke="#979A9C"
            opacity=".4"
            d="M3.5.5h12c1.7 0 3 1.3 3 3v13c0 1.7-1.3 3-3 3h-12c-1.7 0-3-1.3-3-3v-13c0-1.7 1.3-3 3-3z"
          ></path>
          <path fill="#979A9C" d="M11.8 6L8 15.1h-.9L10.8 6h1z"></path>
        </svg>
      </div>
      <input
        className={`form-control form-control-sm border-secondary ${
          searchFocused ? "" : "bg-dark text-white"
        }`}
        type="search"
        name="q"
        placeholder="Search..."
        aria-label="Search"
        ref={inputRef}
        value={inputValue}
        onChange={(event) => setInputValue(event.target.value)}
        onFocus={() => setSearchFocused(true)}
        onBlur={() => setSearchFocused(false)}
      />
    </form>
  );
}
